import React, { useState, useEffect }  from 'react';
import './One.css';
import LogoKabab from "../images/Logo-MagicKebab.png"
import Pain from "../models/Item"
import Galette from "../images/Galette.png"
import check from "../images/checked.png"

const One = () => {
    const [pains, setPain] = useState([])
    

    useEffect(() => {
        fetch("https://run.mocky.io/v3/15eb3221-e824-45d5-a9ca-c5a8455109ff")
          .then(res => res.json())
          .then(res => setPain(res))
      }, [])

    return (
        <>
            <div className="appone">
                <div className="logoone">
                <nav >
        <img
          height="100px"
          src={LogoKabab}
          alt="Logo le bon burger"
          
        />
       
      </nav>
                </div>
                <div className="montext">Pain ou galette ?</div>
                
               
      
                <div className="titrekebab">MAGIC KEBAB</div>
                <div className="container">  
        {pains.map(pain => (
          <Pain key={pain.name} item={pain}  />
        ))}
      </div>
            </div>
        </>
    )
}

export default One
